<template>
  <SwipperButtons>
    <DraggableScroll class="products-carousel hide-scroll-bar">
      <GridGroceryItem
        class="products_carousel__product-item"
        v-for="(product, index) in productListToRender"
        v-bind="product"
        :parent_product_id="+productParentId"
        :key="product ? product.entity_id : index"
        :imgHeight="150"
      />

      <div
        v-if="categoryLink"
        class="products_carousel__product-item products_carousel__see-all-wrapper"
      >
        <router-link class="products_carousel__see-all" :to="categoryLink">{{
          $t("seeMore")
        }}</router-link>
      </div>
    </DraggableScroll>
  </SwipperButtons>
</template>

<script>
import SwipperButtons from "@/components/SwipperButtons";
import GridGroceryItem from "@/components/GridGroceryItem";
import DraggableScroll from "@/components/DraggableScroll";
import get from "@/util/get";

export default {
  name: "ProductsCarousel",
  components: {
    GridGroceryItem,
    DraggableScroll,
    SwipperButtons,
  },
  props: {
    productList: Array,
    productParentId: [String, Number],
    categoryLink: String,
  },
  computed: {
    productListToRender() {
      return get(this.productList, "length") ? this.productList : [...Array(6)];
    },
  },
};
</script>

