    padding-left: 0;
  }

  >>> .items-grid__item {
    padding-bottom: 0;
  }

  >>> .items-grid__content {
    min-height: unset;
  }
}

.products_carousel__see-all-wrapper {
  display: flex;
}

.products_carousel__see-all {
  background-color: #f4f4f4;
