<template>
  <div class="shop-by-cat">
    <h4 class="shop-by-cat__title">{{ $t("categories") }}</h4>
    <div class="shop-by-cat__cat-list hide-scroll-bar">
      <CategoryItem
        v-for="(category, index) in categoriesToRender"
        :key="category.entity_id || index"
        :onClickItem="() => handleClickItem({ name: category.name })"
        :imgURL="category.logo_image"
        :name="category.name"
        :isLoading="!category.name"
      />
    </div>
  </div>
</template>

<script>
import CategoryItem from "@/components/CategoryItem";

export default {
  components: {
    CategoryItem,
  },
  props: {
    categories: {
      type: Array,
      default: () => [],
    },
    nettoURL: String,
  },
  computed: {
    categoriesToRender() {
      if (!this.categories.length) {
        return [...Array(20)].map(e => ({}));
      }
      return this.categories;
    },
  },
  methods: {
    handleClickItem({ name }) {
      this.$router.push(`/verslun/${this.nettoURL}/${name}`);
    },
  },
};
</script>

<style lang="scss" scoped>
.shop-by-cat__title {
  margin-bottom: 16px;
}

.shop-by-cat__cat-list {
  display: flex;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
</style>
