<template>
  <div>
    <ProductModal />
    <div class="header-img">
      <span class="hero-text">{{ storeName }}</span>
    </div>
    <section class="container">
      <div>
        <DeliveryBarWrapper>
          <div class="delivery-bar-wrapper__left-group">
            <!-- <GroceriesChooseFulfillmentMethodContainer /> -->
            <DeliveryCurrAddressContainer
              :sellerType="SELLER_TYPES.GROCERY"
              :customText="currentAddress"
              :supportPickupShipment="supportPickupShipment"
              :storeName="storeName"
              viewName="ViewGroceries"
            />
          </div>
          <MyProductPopupWrapper class="my-product_same-as-last-container">
            <MyProductAndSameAsLastBtns
              :onMyProductClick="handleMyProductClick"
              :merchant="merchant"
              :storeId="id"
            />
          </MyProductPopupWrapper>
        </DeliveryBarWrapper>
        <MyProductAndSameAsLastBtns
          class="view-groceries__md-product-same-as-last show--below-md"
          :onMyProductClick="handleMyProductClick"
          :merchant="merchant"
          :storeId="id"
          usingBtnStyle
          sameAsLastFirst
        />

        <GroceryProducts v-if="id" :id="+id" :storeName="storeName" />
      </div>
    </section>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { SELLER_TYPES, FULFILLMENT_METHOD } from "@/util/constants";
import get from "@/util/get";

import idTracker from "@/mixins/idTracker";
import showHideModal from "@/mixins/showHideModal";
import merchant from "@/mixins/merchant";

import MyProductPopupWrapper from "@/components/MyProductPopupWrapper";
import ProductModal from "@/components/ProductModal";
import DeliveryCurrAddressContainer from "@/components/DeliveryCurrAddressContainer";
import GroceryProducts from "@/components/GroceryProducts";
import DeliveryBarWrapper from "@/components/DeliveryBarWrapper";
import MyProductAndSameAsLastBtns from "@/components/MyProductAndSameAsLastBtns";
import { MODAL_KEY_OUTSIDE_DELIVERY_AREA } from "@/store/modalModule";

export default {
  name: "ViewGroceries",
  mixins: [showHideModal, merchant, idTracker],
  props: {
    storeUrl: String,
  },
  components: {
    GroceryProducts,
    ProductModal,
    DeliveryCurrAddressContainer,
    DeliveryBarWrapper,
    MyProductPopupWrapper,
    MyProductAndSameAsLastBtns,
  },
  data() {
    return {
      address: {},
      SELLER_TYPES,
    };
  },
  computed: {
    ...mapGetters([
      "userAddress",
      "selectedNettoAddress",
      "fulfillmentMethod",
      "getNettoURLByID",
      "getNettoIdByURL",
      "getNettoIdByPostcode",
      "userAddressString",
    ]),
    storeName() {
      return this.id && this.merchant && this.merchant.name;
    },
    id() {
      const { getNettoIdByURL, storeUrl } = this;
      if (!storeUrl) return;
      return getNettoIdByURL(storeUrl);
    },
    restrictToMyProduct() {
      const storeUrl = this.getNettoURLByID(this.id);
      return {
        name: "ViewIndividualProducts",
        params: { isActiveMyProductButton: true, path: storeUrl },
      };
    },
    currentAddress() {
      if (
        this.fulfillmentMethod === FULFILLMENT_METHOD.DELIVERY ||
        !this.supportPickupShipment
      ) {
        return this.currDeliveryText;
      }

      return get(this.merchant, "restaurant_address");
    },
  },
  methods: {
    ...mapActions([
      "setIsInGroceriesScreen",
      "getPickupLocations",
      "setNavBarTitle",
      "fetchStorePostcodeIdMap",
      "notification",
      "toggleModal",
      "getStoreIdURLMap",
    ]),
    handleMyProductClick() {
      if (!this.$store.state.customer.is_logged_in) {
        this.$store.dispatch("toggleLogin", this.restrictToMyProduct);
        return;
      }
      this.$router.push(this.restrictToMyProduct);
    },
    async goToNettoStore() {
      const {
        fulfillmentMethod,
        userAddress,
        selectedNettoAddress,
        getNettoURLByID,
        getNettoIdByPostcode,
        fetchStorePostcodeIdMap,
        getStoreIdURLMap,
      } = this;
      const { postcode } = userAddress;
      await fetchStorePostcodeIdMap();
      await getStoreIdURLMap();
      let storeID = getNettoIdByPostcode(postcode);
      if (fulfillmentMethod === FULFILLMENT_METHOD.PICKUP) {
        // if we are selecting pickup in other screen and don't have a selected netto
        // select store by user address instead
        storeID = selectedNettoAddress ? selectedNettoAddress.id : storeID;
      }
      if (!storeID) return;
      const storeUrl = getNettoURLByID(storeID);
      this.$router.replace(`/grocery/${storeUrl}`);
    },
    async outsideDeliveryAreaWarning({ userAddress = this.userAddress } = {}) {
      const { getNettoIdByPostcode, fetchStorePostcodeIdMap } = this;
      const { postcode } = userAddress;
      await fetchStorePostcodeIdMap();
      const storeId = getNettoIdByPostcode(postcode);

      if (!storeId) {
        this.toggleModal({
          modalKey: MODAL_KEY_OUTSIDE_DELIVERY_AREA,
          isVisible: true,
        });
      }
    },
  },
  async mounted() {
    const { userAddressString, storeUrl } = this;
    // now we can toggle groceries product modal
    this.setNavBarTitle({
      en: "Grocery",
      is: "Matvara",
    });
    this.setIsInGroceriesScreen(true);
    this.getPickupLocations();

    if (!storeUrl && userAddressString) {
      await this.goToNettoStore();
    }
  },
  beforeDestroy() {
    this.setIsInGroceriesScreen(false);
  },
  watch: {
    userAddress() {
      this.outsideDeliveryAreaWarning();
    },
    fulfillmentMethod(newFulfillmentMethod) {
      if (
        newFulfillmentMethod === FULFILLMENT_METHOD.DELIVERY &&
        this.userAddressString
      ) {
        this.outsideDeliveryAreaWarning();
      }
    },
  },
};
</script>

