<template>
  <div class="add-to-card-with-quantity-control">
    <QuantityControl
      class="add-to-card__quantity-control"
      :updateQuantity="updateQuantity"
      :quantity="quantity"
      :min="1"
    />
    <BaseButton
      class="primary add-to-card__add-btn"
      @click.native="submitAddCart"
    >
      {{ $t(inCartQty ? "updateCartQty" : "addToCart") }}
      <span class="add-to-card__total-price">{{
        (price * quantity) | formatISK
      }}</span>
    </BaseButton>
  </div>
</template>

<script>
import BaseButton from "@/components/BaseButton";
import QuantityControl from "@/components/QuantityControl";

export default {
  name: "AddToCartRepresentation",
  components: {
    BaseButton,
    QuantityControl,
  },
  props: {
    quantity: Number,
    updateQuantity: Function,
    submitAddCart: Function,
    price: Number,
    inCartQty: Number,
  },
};
</script>

