<template>
  <BaseModal
    :isVisible="isProductModalVisible"
    :centeredTitle="true"
    :noFooter="true"
    @hide="hideProductModal"
    class="product-modal"
    size="lg"
  >
    <div class="product-modal__content">
      <div class="row">
        <div
          class="col-md-6 product-modal__product-img-wapper"
          v-if="cloudinaryImgURL"
        >
          <img
            class="product-modal__product-img"
            :src="cloudinaryImgURL"
            :alt="payload.name"
          />
        </div>
        <div :class="`col-md-${cloudinaryImgURL ? '6' : '12'}`">
          <h4 class="product-modal__product-name">{{ payload.name }}</h4>
          <div class="product-modal__product-price-container">
            <span
              v-if="payload.calc_special_price"
              class="product-modal__special-price"
              >{{ payload.calc_special_price | formatISK }}</span
            >
            <span class="product-modal__price">{{
              payload.calc_price | formatISK
            }}</span>
          </div>

          <div class="product-modal__unit">{{ payload.unit_text }}</div>

          <div class="product-modal__ingredients" v-if="payload.ingredients">
            <strong>{{ $t("ingredients") }}:</strong>&nbsp;
            <span>{{ payload.ingredients }}</span>
          </div>

          <div
            :class="[
              'product-modal__notification-bar',
              !payload.inCartQty && 'visibility-hidden',
            ]"
          >
            <i class="icon icon--check-fill" />
            <span class="product-modal__notification-message"
              >{{ payload.inCartQty }} item is already in your cart</span
            >
          </div>
          <AddToCartRepresentation
            :price="payload.calc_price"
            :quantity="tempQuantity"
            :updateQuantity="updateQuantity"
            :submitAddCart="submitAddCart"
            :inCartQty="payload.inCartQty"
          />
        </div>
      </div>
    </div>
  </BaseModal>
</template>
<script>
import BaseModal from "@/components/BaseModal";
import AddToCartRepresentation from "@/components/AddToCartRepresentation";
import { MODAL_KEY_PRODUCT } from "@/store/modalModule";
import { mapGetters } from "vuex";
import CloudinaryQuery from "@/util/CloudinaryQuery";

export default {
  name: "ProductModal",
  components: {
    BaseModal,
    AddToCartRepresentation,
  },
  data: function() {
    return {
      tempQuantity: 0,
    };
  },
  computed: {
    ...mapGetters(["getModalState", "getModalVisibilityState"]),
    isProductModalVisible() {
      return this.getModalVisibilityState(MODAL_KEY_PRODUCT);
    },
    payload() {
      return this.getModalState(MODAL_KEY_PRODUCT);
    },
    cloudinaryImgURL() {
      return (
        this.payload.productImg &&
        new CloudinaryQuery(this.payload.productImg).height(400).toURL()
      );
    },
  },
  methods: {
    submitAddCart() {
      const { updateCartQuantity } = this.payload;
      if (!updateCartQuantity) return;

      updateCartQuantity(this.tempQuantity);
      this.hideProductModal();
    },
    updateQuantity(newQuantity) {
      if (newQuantity < 1) {
        newQuantity = 1;
      }
      this.tempQuantity = newQuantity;
    },
    hideProductModal() {
      this.$store.dispatch("toggleModal", {
        isVisible: false,
        modalKey: MODAL_KEY_PRODUCT,
      });
    },
  },
  watch: {
    isProductModalVisible(newState, oldState) {
      if (newState && !oldState) {
        this.tempQuantity = this.payload.inCartQty || 1;
      }
    },
  },
};
</script>

