<template>
  <div class="grid-carousel-row">
    <div v-if="!categoryName" class="">
      <div class="grid-carousel-row__categories-container hide-scroll-bar">
        <div
          class="grid-carousel-row__category-item grid-carousel-row__category-item--main app-ph"
        />
      </div>
      <ProductsCarousel :productList="category" :productParentId="storeId" />
    </div>
    <div v-else class="">
      <div class="grid-carousel-row__categories-container hide-scroll-bar">
        <router-link
          class="grid-carousel-row__category-item grid-carousel-row__category-item--main"
          :to="linkParams(categoryName)"
          title="Skoða flokk nánar"
        >{{ categoryName }}</router-link>

        <router-link
          class="grid-carousel-row__category-item"
          v-for="subcat in subcategories(category)"
          :to="linkParamsSubcategory(categoryName, subcat.name)"
          :key="subcat.entity_id"
        >
          <BaseButton class="tertiary tertiary--secondary" title="category">{{ subcat.name }}</BaseButton>
        </router-link>
      </div>
      <ProductsCarousel
        :productList="category"
        :productParentId="storeId"
        :categoryLink="linkParams(categoryName)"
      />
    </div>
  </div>
</template>

<script>
import BaseButton from "@/components/BaseButton";
import ProductsCarousel from "@/components/ProductsCarousel";
import get from "@/util/get";

export default {
  name: "GridCarouselRow",
  props: {
    categories: Array,
    category: Array,
    nettoURL: String,
    storeId: undefined,
  },
  components: {
    BaseButton,
    ProductsCarousel,
  },
  computed: {
    categoryName() {
      return get(this.category, "0.catname");
    },
  },
  methods: {
    subcategories(category) {
      return this.categories.filter(function(c) {
        if (c.category_id == category[0].category_id) {
          return true;
        }
      });
    },
    linkParams(name) {
      return `/verslun/${this.nettoURL}/${name}`;
    },
    linkParamsSubcategory(category, subcategory) {
      return `/verslun/${this.nettoURL}/${category}/${subcategory}`;
    },
  },
};
</script>

