<template>
  <div
    :class="[
      'filter-and-options__option-container',
      isShowMorePopupVisible && 'filter-and-options__option-container--show',
    ]"
    @click="toggleShowMorePopup"
  >
    <div class="filter-and-options__options" @click.stop="">
      <slot />
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { MODAL_KEY_MORE_OPTIONS_POP_UP } from "@/store/modalModule";

export default {
  name: "MyProductPopupWrapper",
  computed: {
    ...mapGetters(["getModalVisibilityState"]),
    isShowMorePopupVisible() {
      return this.getModalVisibilityState(MODAL_KEY_MORE_OPTIONS_POP_UP);
    },
  },
  methods: {
    ...mapActions(["toggleModal"]),
    toggleShowMorePopup() {
      this.toggleModal({
        modalKey: MODAL_KEY_MORE_OPTIONS_POP_UP,
      });
    },
  },
};
</script>

