<template>
  <div class="grocery-products">
    <ShopByCategories
      class="grocery-products__categories category-item--text-align-center"
      :categories="topLvCategories"
      :nettoURL="nettoURL"
    />

    <div :class="['grocery-products__carousels-container']">
      <GridCarouselRow
        v-for="(category, index) in categoriesToRender"
        :key="categoryId(category, index)"
        :category="category"
        :categories="categories"
        :nettoURL="nettoURL"
        :storeId="id"
      />
    </div>
  </div>
</template>

<script>
import chunk from "@/util/chunk";
import fetchItems from "@/util/fetchItems";
import get from "@/util/get";
import { mapGetters, mapActions } from "vuex";
import { LOADING_KEY_FETCH_GROCERY_PRODUCT } from "@/store/loadingModule";
import GridCarouselRow from "@/components/GridCarouselRow";
import ShopByCategories from "@/components/ShopByCategories";

export default {
  name: "GroceryProducts",
  components: {
    GridCarouselRow,
    ShopByCategories,
  },
  props: {
    id: Number,
    storeName: String,
  },
  data() {
    return {
      groupedCategories: [],
      categories: [],
      LOADING_KEY_FETCH_GROCERY_PRODUCT,
    };
  },
  computed: {
    ...mapGetters(["getNettoURLByID"]),
    categoriesToRender() {
      if (!this.groupedCategories.length) {
        return [...Array(6)];
      }
      const topLvCategoryIds = [];
      this.groupedCategories.forEach((product, index) => {
        if (index === 0) {
          return topLvCategoryIds.push(product.category_id);
        }
        if (
          product.category_id !== topLvCategoryIds[topLvCategoryIds.length - 1]
        ) {
          return topLvCategoryIds.push(product.category_id);
        }
      });

      return topLvCategoryIds.map(catId =>
        this.groupedCategories.filter(product => product.category_id === catId)
      );
    },
    nettoURL() {
      const { getNettoURLByID, id } = this;
      return getNettoURLByID(id);
    },
    topLvCategories() {
      const topLvCatIds = this.categoriesToRender.map(productList =>
        get(productList, "0.category_id")
      );
      const topLvCategories = topLvCatIds
        .map(catId =>
          this.categories.find(({ entity_id }) => entity_id === catId)
        )
        .filter(Boolean);
      return topLvCategories;
    },
  },
  methods: {
    ...mapActions(["toggleLoading"]),
    chunk,
    categoryId(category, index) {
      return get(category, "0.category_id", index);
    },
    async fetchProducts() {
      // TODO: Refactor - chunk might give incorrect results if webservice is returning less or more than 6 items per category - skip chunk and group array by categories
      const { toggleLoading } = this;
      toggleLoading({
        loadingKey: LOADING_KEY_FETCH_GROCERY_PRODUCT,
        isLoading: true,
      });

      const res = await fetchItems({
        service: "store_frontpage",
        store_id: this.id,
        cache: 600,
      });
      this.groupedCategories = Object.values(res.data["0"]).map(o => ({
        stores: [{ store_id: this.id, store_name: this.storeName }],
        ...o,
      }));
      this.categories = Object.values(res.data["1"]);

      toggleLoading({
        loadingKey: LOADING_KEY_FETCH_GROCERY_PRODUCT,
        isLoading: false,
      });
    },
  },
  watch: {
    async id() {
      this.groupedCategories = [];
      this.categories = [];
      await this.fetchProducts();
    },
    storeName() {
      const items = this.groupedCategories.map(item => {
        return {
          ...item,
          stores: [
            { store_id: item.stores[0].store_id, store_name: this.storeName },
          ],
        };
      });
      this.groupedCategories = items;
    },
  },
  async mounted() {
    await this.fetchProducts();
  },
};
</script>

<style lang="scss" scoped>
.inline-block {
  display: inline-block;
  float: none;
  vertical-align: top;
}

.grocery-products__categories {
  margin-bottom: 22px;
}
</style>
